import React, {
  createContext,
  useState,
  useEffect,
  useCallback,
  useMemo,
} from 'react'

export const UserContext = createContext(null)

const API_TOKEN =
  'Bearer eyJhbGciOiJIUzI1NiJ9.eyJhdWQiOiIzYzEyNTlmNzkxZTY0N2U3M2Y1N2FhNTUxY2FiNDM3YyIsIm5iZiI6MTcxODYyMjcxMy4wMTcsInN1YiI6IjY2NzAxOWY5MjdmYmY4MDViNzE5MjhiYyIsInNjb3BlcyI6WyJhcGlfcmVhZCJdLCJ2ZXJzaW9uIjoxfQ.bMVx70j1s3K2TUJKJueOxs5NXBhLeM_6Q6LzzhIaB54'

export const UserProvider = ({ children }) => {
  const [token, setToken] = useState(localStorage.getItem('userToken') || '')
  const [accountId, setAccountId] = useState(
    localStorage.getItem('accountId') || ''
  )
  const [favoriteMovies, setFavoriteMovies] = useState([])

  //    
  const fetchFavorites = useCallback(
    async (accId = accountId) => {
      if (!token || !accId) return
      try {
        const response = await fetch(
          `https://api.themoviedb.org/3/account/${accId}/favorite/movies`,
          {
            method: 'GET',
            headers: {
              Authorization: API_TOKEN,
              'Content-Type': 'application/json',
            },
          }
        )
        const data = await response.json()
        setFavoriteMovies(data.results || [])
      } catch (error) {
        console.error('   :', error)
      }
    },
    [token, accountId]
  ) // `fetchFavorites`      `token`  `accountId`

  //   accountId
  const fetchAccountId = useCallback(async () => {
    if (!token) return
    try {
      const response = await fetch(`https://api.themoviedb.org/3/account`, {
        method: 'GET',
        headers: {
          Authorization: API_TOKEN,
          'Content-Type': 'application/json',
        },
      })
      const data = await response.json()
      if (data.id) {
        setAccountId(data.id)
        localStorage.setItem('accountId', data.id)
        fetchFavorites(data.id)
      }
    } catch (error) {
      console.error('  account_id:', error)
    }
  }, [token, fetchFavorites]) // `fetchAccountId`     `token`  `fetchFavorites`

  //     `token`
  useEffect(() => {
    if (token) {
      localStorage.setItem('userToken', token)
      fetchAccountId()
    } else {
      localStorage.removeItem('userToken')
    }
  }, [token, fetchAccountId]) //   

  //  /  
  const toggleFavorite = useCallback(
    async (movie) => {
      if (!token || !accountId) return
      const isFavorite = favoriteMovies.some((fav) => fav.id === movie.id)

      try {
        const response = await fetch(
          `https://api.themoviedb.org/3/account/${accountId}/favorite`,
          {
            method: 'POST',
            headers: {
              Authorization: API_TOKEN,
              'Content-Type': 'application/json',
            },
            body: JSON.stringify({
              media_type: 'movie',
              media_id: movie.id,
              favorite: !isFavorite,
            }),
          }
        )

        if (response.ok) {
          fetchFavorites() //    
        }
      } catch (error) {
        console.error('  :', error)
      }
    },
    [token, accountId, favoriteMovies, fetchFavorites]
  ) // `toggleFavorite`     

  //  value,     
  const contextValue = useMemo(
    () => ({
      token,
      setToken,
      favoriteMovies,
      toggleFavorite,
    }),
    [token, favoriteMovies, toggleFavorite]
  ) //      

  return (
    <UserContext.Provider value={contextValue}>{children}</UserContext.Provider>
  )
}
