import React, { createContext, useReducer, useContext } from 'react'

const FiltersContext = createContext(null)

const initialState = {
  genres: [],
  selectedGenres: [],
  releaseYear: '',
  sortBy: 'popular', //   
  currentPage: 1, //  
  totalPages: 1, //  
  searchQuery: '', //     
}

const filterReducer = (state, action) => {
  switch (action.type) {
    case 'SET_GENRES':
      return { ...state, genres: action.payload }
    case 'SET_SELECTED_GENRES':
      return { ...state, selectedGenres: action.payload }
    case 'SET_RELEASE_YEAR':
      return { ...state, releaseYear: action.payload }
    case 'SET_SORT_BY':
      return { ...state, sortBy: action.payload, currentPage: 1 } //       
    case 'SET_PAGE':
      return { ...state, currentPage: action.payload } //   
    case 'SET_TOTAL_PAGES':
      return { ...state, totalPages: action.payload } //    
    case 'SET_SEARCH_QUERY': //   
      return { ...state, searchQuery: action.payload, currentPage: 1 } //      
    case 'RESET_FILTERS':
      return { ...initialState, genres: state.genres } //   
    default:
      return state
  }
}

export const FiltersProvider = ({ children }) => {
  const [state, dispatch] = useReducer(filterReducer, initialState)

  return (
    <FiltersContext.Provider value={{ state, dispatch }}>
      {children}
    </FiltersContext.Provider>
  )
}

export const useFilters = () => useContext(FiltersContext)
